/* theme */
.lbtn.lbtn-twitter {
    color: #ffffff;
    background-color: #1da1f2;
    border-color: #349ad9;
}
.lbtn.lbtn-twitter.white {
    color: #1da1f2;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-twitter .logo {
    background: url(../logo/twitter.svg);
}
.lbtn.lbtn-twitter.lbtn-fallback-svg .logo {
    background: url(../logo/twitter.png);
}